@font-face {
	font-family: ERAS;
	src: url(fonts/ERASBD.TTF);
}

body {
	overflow-x: hidden; /* Hide scrollbars */
	font-family: "ERAS";
}
button {
	font-family: "ERAS";
}

.topbar {
	position: sticky;
	top: 0px;
	background-color: #ff7675;
	height: 60px;
	z-index: 1;
	text-align: right;
}
.languageButton {
	font-size: 30px;

	margin-top: 10px;
	margin-right: 10px;
	background: none;
	border: none;
}

.Gradient {
	position: relative;
	height: 300px;
	background: rgb(250, 177, 160);
	background: linear-gradient(
		156deg,
		rgba(250, 177, 160, 1) 0%,
		rgba(162, 155, 254, 1) 100%
	);
}

.background {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;

	z-index: -1;
}

@keyframes fromRight {
	from { color: transparent; margin-left: 25%; }
	to { color: white; margin-left: 20%; }
}
@keyframes fromBottom {
	from { color: transparent; transform: translate(0px, 25px); }
	to { color: white; transform: translate(0px, 0px);; }
}

.link {
	color:#2D3436;
	text-decoration: underline;
	font-size:20px;
}
.center {
	text-align:center;
	margin: auto;
	width: 50%;
}
.centerRight {
	text-align:right;
	margin: auto;
	padding: 7px;
}

.header {
	animation: fromRight 1s ease-out 0.5s;
	animation-fill-mode: both;

	text-align: left;
	margin-top: 0px;
	vertical-align: 50%;

	top: 50%;
	position: absolute;
	transform: translate(0px, -50%);

	font-size: 50px;
}

.projectsText {
	font-size: 36px;
	margin-right: 18%;
	margin-left: 18%;
}
.contentParent {
	animation: fromBottom 0.7s ease-out 0.3s;
	animation-fill-mode: both;

	margin-top: 30px;
	margin-bottom: 0px; /* No margin at bottom cause bg won't go there */
	margin-right: 10%;
	margin-left: 10%;

	text-align: center;
}

.project {
	text-decoration: none;

	width: 100%;
	height: 100%;

	border: none;
	border-radius: 10px;
	text-align: left;

	transition: all 0.2s ease;

	display: block;
	position: absolute;
}

.realHitbox {
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 1;

	display:block;
}

.projectParent {
	/* Golden ratio */
	height: 275px;
	width: 445px;

	margin-left: 30px;
	margin-right: 30px;
	margin-top: 0px;
	margin-bottom: 100px;

	display: inline-block;
	vertical-align: bottom;
	position: relative;
}

.realHitbox:hover + .project{
	transform: translate(0px, -5%);
}
.projectOpenText {
	text-align: right;
	position: relative;
	margin: 0px;
	padding: 20px;
	
	top: -80px;
	left: 0px;
	font-size: 30px;
	
	transition: all 0.2s ease;
	width: calc(100% - 40px - 10%);
	color: transparent;
	
}
.realHitbox:hover + .project .projectOpenText {
	text-align: right;
	width: calc(100% - 40px - 0%);
	color: #2D3436;
}

.projectRightSide {
    padding: 20px;
	width: calc(30% - 40px);
	display: inline-block;
	vertical-align: top;
}
.projectHeader {
	color: #2D3436;
	margin-bottom: 5px;
	margin-top: 0px;
}
.projectDescription {
	color: black;
	text-align: top;
	padding-left: 10px;
}
.projectLeftSide {
	position: static;
	top: 0px;
	height: calc(100% - 40px); /* include padding */

	padding: 20px;
	width: calc(70% - 40px);
	display: inline-block;
}
.projectImage {
	width: 100%;
	border-radius: 20px;
}
